import React from 'react';
import StyledNavWrapper from './StyledNavWrapper';
import HamburgerButton from 'components/molecules/HamburgerButton/HamburgerButton';
import Nav from 'components/organisms/Nav/Nav';

const NavWrapper = ({ selectTabFunction, navArr, isNavOpen, toggleNavVisibility, selectedTab }) => {
  return (
    <StyledNavWrapper>
      <Nav
        toggleNavVisibility={toggleNavVisibility}
        isNavOpen={isNavOpen}
        navArr={navArr}
        selectedTab={selectedTab}
        selectTabFunction={selectTabFunction}
      />
      <HamburgerButton toggleNavVisibility={toggleNavVisibility} isNavOpen={isNavOpen} />
    </StyledNavWrapper>
  );
};
export default NavWrapper;
