import styled from 'styled-components';

const StyledNav = styled.nav`
  z-index: 2;
  background-color: ${({ theme }) => theme.colors.navBg};
  height: 100vh;
  transition: width 1s;

  &.hidden {
    display: none;
  }
`;

export const StyledUl = styled.ul`
  margin-top: 1rem;
`;

export default StyledNav;
